.button {
  display: inline-flex;
  padding: 20px 40px;
  justify-content: center;
  align-items: center;
  gap: 20px;
  border-radius: 10px;
  border: 1px solid #fff;
  background: #283760;
  box-shadow: 0px 2px 10px 0px rgba(40, 55, 96, 0.1);
  color: var(--white, #fff);
  text-align: center;
  font-family: "Noto Sans Hebrew";
  font-size: 30px;
  font-style: normal;
  font-weight: 900;
  line-height: 109%;
  height: 105px;
}

button:disabled {
  background-color: #aeafb1;
  box-shadow: none;
}

.button.outline {
  border: 2px solid #283760;
  background: #dae2ed;
  color: #283760;
}

